'use client'

import { useState } from 'react'
import { Product } from '@/data/products'

interface ProductCardProps {
  product: Product
}

export default function ProductCard({ product }: ProductCardProps) {
  const [currentImageIndex, setCurrentImageIndex] = useState(0)

  return (
    <div className="bg-gray-900 rounded-lg overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300">
      {/* Image Section */}
      <div className="relative">
        <div className="aspect-[4/5] bg-gray-800 flex items-center justify-center">
          {product.images.length > 0 ? (
            <div 
              className="w-full h-full bg-gray-700 flex items-center justify-center text-gray-400"
              style={{
                backgroundImage: `url(${product.images[currentImageIndex]})`,
                backgroundSize: 'cover',
                backgroundPosition: 'center'
              }}
            >
              <span className="text-sm">Imagen del Producto</span>
            </div>
          ) : (
            <div className="text-gray-400 text-center">
              <div className="text-4xl mb-2">ðŸ“·</div>
              <span className="text-sm">Sin imagen</span>
            </div>
          )}
        </div>

        {/* Badges */}
        <div className="absolute top-2 left-2 flex flex-col gap-1">
          {product.new && (
            <span className="bg-red-600 text-white text-xs font-bold px-2 py-1 rounded">
              NUEVO
            </span>
          )}
          {product.favorite && (
            <span className="bg-white text-black text-xs font-bold px-2 py-1 rounded">
              FAVORITO
            </span>
          )}
        </div>

        {/* Image Dots */}
        {product.images.length > 1 && (
          <div className="absolute bottom-2 left-1/2 transform -translate-x-1/2 flex gap-1">
            {product.images.map((_, index) => (
              <button
                key={index}
                onClick={() => setCurrentImageIndex(index)}
                className={`w-2 h-2 rounded-full transition-colors ${
                  index === currentImageIndex
                    ? 'bg-white'
                    : 'bg-gray-400 hover:bg-gray-300'
                }`}
                aria-label={`Ver imagen ${index + 1}`}
              />
            ))}
          </div>
        )}
      </div>

      {/* Product Info */}
      <div className="p-4">
        <h3 className="text-white font-semibold text-lg mb-1 line-clamp-2">
          {product.name}
        </h3>
        <p className="text-gray-400 text-sm mb-2">
          {product.collection}
        </p>
        <div className="flex items-center justify-between">
          <span className="text-red-600 font-bold text-xl">
            ${product.price.toFixed(2)}
          </span>
          <button className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded text-sm font-medium transition-colors">
            Agregar
          </button>
        </div>
      </div>
    </div>
  )
}
